import React, { useRef, useState } from 'react'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Equip_State, Greek_Character, StyledContent, StyledOverlay, isModal } from './Equip_table';
import { Box} from '@mui/material';
import styled from '@emotion/styled';
import PopUp from './PopUp';

const rows = [
  {name: "", value:[0]},
  {name: "7",value:[0,0,0]},
  {name: "7",value:[0,0,0]},
  {name: "7",value:[0,0,0]},
  {name: "7",value:[0,0,0]},
  {name: "45",value:[0,0,0]},
  {name: "45",value:[0,0,0]},
  {name: "45",value:[0,0,0]},
  {name: "45",value:[0,0,0]},
  {name: "45",value:[0,0,0]},
  {name: "6",value:[0,0,0]},
  {name: "6",value:[0,0,0]},
  {name: "12",value:[0,0,0]},
  {name: "",value:[0,0,0]}];

const Table_Header = (props) => {
    return(
      <>
        {Greek_Character.map((value, index) => {
            if(index < props.num) return(<TableCell align='left' colSpan={4}>{value}</TableCell>)
          })}
      </>
    )
}

const Table_Header_Element = (num) => {
  return(
    <>
    {Greek_Character.map((value, index) => {
        if(index < num) return(
          <>
          <TableCell align='right'></TableCell>
          <TableCell align='right'></TableCell>
          <TableCell align='right'></TableCell>
          <TableCell></TableCell>
          </>
        )
      })}
    </>
  )
}



function Tent_Table() {
  
  const [isModal, SetIsModal] = useState(false);
  const information = useRef(null);
  const PopUpWrapper = useRef(null);

  const closeModal = (e) => {
    let elm = e.target;
    let flag = 0;
    while(elm != null){
      if(elm.className != undefined && elm.className.startsWith("PopUp")) {
        flag=1;
        break;
      }
      elm = elm.parentNode;
    }
    if(flag == 1) return;
    SetIsModal(false);
    document.removeEventListener("click", (e) => {closeModal(e)})
  }
  
  function Change_State(info,event){
    SetIsModal(true);
    information.current = info;
    document.addEventListener("click", closeModal);
    event.stopPropagation();
  }

  const Equip_State = (place,name) => {
    const info = {
      place: place,
      color: "",
      name: name
    };
    if(place === 0){
      info.place = ""
      info.color = "gray"
    }else if(place === 1){
      info.place = ""
      info.color = "blue"
   }else if(place === 2){
      info.place = ""
      info.color = "red"
    }else info.place = ""
    return (
      <Box style={{cursor: "default"}} onClick= {(event) => {Change_State(info, event)}}>
      <Box style={{backgroundColor: `${info.color}`, padding: "4px", color:"white", textAlign: "center"}}>{info.name}</Box>
      </Box>
    );
  }
  return (
  <>
    {isModal===true ? <PopUp information={information.current} ref={PopUpWrapper}/> : <></>}
    <TableContainer component={Paper} >
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
                <TableRow>
                  <TableCell align="center" colSpan={2} style={{borderBottom: "none"  }}></TableCell>
                  <TableCell align='left' colSpan={16}>7</TableCell>
                  <TableCell align='left' colSpan={20}>45</TableCell>
                  <TableCell align='left' colSpan={8}>6</TableCell>
                  <TableCell align='left' colSpan={4}>12</TableCell>
                  <TableCell align='left' colSpan={4}></TableCell>
                </TableRow>
                <TableRow>
                  <TableCell align='center' colSpan={1} ></TableCell>
                  <TableCell></TableCell>
                  {Table_Header({num: 4})}
                  {Table_Header({num: 5})}
                  {Table_Header({num: 2})}
                  {Table_Header({num: 1})}
                  {Table_Header({num: 1})}
                </TableRow>
                <TableRow>
                  <TableCell align='center'></TableCell>
                  <TableCell></TableCell>
                  {Table_Header_Element(4)}
                  {Table_Header_Element(5)}
                  {Table_Header_Element(2)}
                  {Table_Header_Element(1)}
                  {Table_Header_Element(1)}
                </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
              {rows.map((row) => (
                  row.value.map((val,index) => {
                    if(index == row.value.length-1){
                      return (
                      <>
                        <TableCell align='right'>{Equip_State(0,"")}</TableCell>
                        <TableCell></TableCell>
                      </>)
                    }else{
                      return (<TableCell align='right'>{Equip_State(0,"")}  </TableCell>)
                    }
                  })
                ))}
              </TableRow>
            </TableBody>
        </Table>
    </TableContainer>
  
    </>
  )
}


export default Tent_Table