import styled from "@emotion/styled";
import {AppBar, Toolbar, Typography, Button} from "@mui/material";
import React from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import styles from './styles/Home.module.css'
import { Link } from "react-router-dom";

const StyledTypography = styled(Typography)(() => ({
    fontSize: "34px",
    flexGrow: 1,
    padding: "10px 20px"
}))
const StyledButton = styled(Button)(() => ({
    color: "white",
    flexGrow: 0.05,
    fontSize: "15px",
    "&:hover": {
        opacity: "0.5",
    }
}))

const Bottondiv = styled("div")(() => ({
    color: "white",
    border: "none",
    display:"inline",
    alignItems: "center",
    textDecoration: "none",
    "&:hover":{
        opacity: "0.5"
    }
}))
function Header() {
  return (
    <div>
        <AppBar position="static">
            <Toolbar className={styles.ToolBar}>
                <MenuIcon />
                {/* <StyledTypography component="div"><Alink href="/">!</Alink></StyledTypography>
                <StyledButton sx={{marginLeft: "auto"}} href="/F" >F</StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Max">Max&Limit</StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Table"></StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Return"></StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Create">E</StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Modify">E</StyledButton>
                <StyledButton sx={{marginLeft: "auto"}} href="/Other"></StyledButton> */}
                
              <Link to = {'/'} style={{marginLeft:"20px", fontSize: "1.8rem", marginRight:"20px", textDecoration:"none"}}><Bottondiv></Bottondiv></Link>  
              <Link to = {'/F'} className={styles.HeaderLinkElement} style={{ marginLeft: "auto",textDecoration:"none"}}><Bottondiv>F</Bottondiv></Link>
              <Link to = {'/Max'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv>Max</Bottondiv></Link>
              <Link to = {'/Table'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv>E</Bottondiv></Link>
              <Link to = {'/Return'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv></Bottondiv></Link>
              <Link to = {'/Create'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv>E</Bottondiv></Link>
              <Link to = {'/Modify'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv>E</Bottondiv></Link>
              <Link to = {'/Other'} className={styles.HeaderLinkElement} style={{textDecoration:"none"}}><Bottondiv></Bottondiv></Link>
            </Toolbar>            
        </AppBar>
    </div>
  )
}

export default Header