import React, { useRef, useState } from 'react'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { Greek_Character} from './Equip_table';
import { Box } from '@mui/material';
import PopUp from './PopUp';

const rows = [
  {name:"", value:[0]}, 
  {name:"L", value:[0]},
  {name:"L", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
  {name:"", value:[0]},
];

const Table_Header = (props) => {
    return(
      <>
        {
          Greek_Character.map((value, index) => {
            if(index < props.num){
              return(
                <>
                <TableCell>{value}</TableCell>
                </>
              )
            }
          })
        }
      </>
    )
}


function Work_Saw_Table() {

  
  const [isModal, SetIsModal] = useState(false);
  const information = useRef(null);

  const closeModal = (e) => {
    let elm = e.target;
    let flag = 0;
    while(elm != null){
      if(elm.className != undefined && elm.className.startsWith("PopUp")) {
        flag=1;
        break;
      }
      elm = elm.parentNode;
    }
    if(flag == 1) return;
    SetIsModal(false);
    document.removeEventListener("click", (e) => {closeModal(e)})
  }

  function Change_State(info,event){
    SetIsModal(true);
    information.current = info;
    document.addEventListener("click", closeModal);
    event.stopPropagation();
  }

  const Equip_State = (place,name) => {
    const info = {
      place: place,
      color: "",
      name: name
    };
    if(place === 0){
      info.place = ""
      info.color = "gray"
    }else if(place === 1){
      info.place = ""
      info.color = "blue"
   }else if(place === 2){
      info.place = ""
      info.color = "red"
    }else info.place = ""
    return (
      <Box style={{cursor: "default"}} onClick= {(event) => {Change_State(info, event)}}>
      <Box style={{backgroundColor: `${info.color}`, padding: "4px", color:"white", textAlign: "center"}}>{info.name}</Box>
      </Box>
    );
  }
  return (
    <>
    {isModal===true ? <PopUp information = {information.current}/> : <></>}
    <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell colSpan={2}></TableCell>
                <TableCell colSpan={3}>L</TableCell>
                <TableCell colSpan={5}></TableCell>
                <TableCell colSpan={4}></TableCell>
                <TableCell colSpan={7}></TableCell>
                <TableCell colSpan={3}></TableCell>
                <TableCell colSpan={3}></TableCell>
              </TableRow>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                {Table_Header({name:"L",num:2})}
                <TableCell></TableCell>
                {Table_Header({name:"",num:4})}
                <TableCell></TableCell>
                {Table_Header({name:"",num:3})}
                <TableCell></TableCell>
                {Table_Header({name:"",num:6})}
                <TableCell></TableCell>
                {Table_Header({name:"",num:2})}
                <TableCell></TableCell>
                {Table_Header({name:"",num:6})}
            </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                  row.value.map((val,index) => {
                    if(row.name === '' || row.name === 'L' || row.name === '' || row.name === '' || row.name ==='' || row.name ==='' || row.name ===''){
                      return (
                      <>
                        <TableCell align='left'>{Equip_State(0,"")}</TableCell>
                        <TableCell align='left'></TableCell>
                      </>)
                    }else{
                      return (<TableCell align='left'>{Equip_State(0,"")}</TableCell>)
                    }
                  })
                ))}
            </TableBody>
        </Table>
    </TableContainer>
    </>
  )
}


export default Work_Saw_Table